<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'
import * as THREE from 'three'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: false, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'OrbitControls'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          }
        }
      }
    }
  },
  mounted () {
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      camera.position.set(600, 400, 600) // 修改相机位置

      this.clock = new THREE.Clock()
    })
    /**
     * 导入模型
     */
    app.addModel({
      url: 'static/model/glb/Horse.glb', // 模型地址
      type: 'glb', // 模型格式
      name: 'horse',
      animations: true
    }, (obj, animations, mixer, actions) => { // 加载完成回调，可忽略。
      console.log('模型加载完成')

      // 循环渲染
      app.animate(() => {
        var dt = this.clock.getDelta()
        if (mixer) mixer.update(dt)
      })

      actions['horse_A_'].play() // 播放动画
    })
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
